import PropTypes from 'prop-types';
import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import {
  Badge,
  Card,
  CardBody,
  Col,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Row,
  UncontrolledDropdown
} from 'reactstrap';

const NavbarDropdownComponents = ({ title, items, right, children }) => {
  const arraySlicer = routes => {
    const newArr = [];
    routes.children.map(route => {
      if (route.children) {
        return newArr.push(...route.children);
      }
      return newArr.push(route);
    });

    const arrayDivide = Math.ceil(newArr.length / 4);

    const arrayLength = newArr.length;
    const temp = [];
    for (let i = 0; i < arrayLength; i += arrayDivide) {
      var myArray = newArr.slice(i, i + arrayDivide);
      temp.push(myArray);
    }

    return temp;
  };

  // const NavItemGroup = title !== 'Home' && arraySlicer(items);
  // console.log(NavItemGroup);

  return (
    <UncontrolledDropdown nav inNavbar className="dropdown-menu-on-hover">
      <DropdownToggle nav caret>
        {title}
      </DropdownToggle>
      <DropdownMenu right={right} className="dropdown-menu-card mt-0">
        {items.length > 0 && (
          <Card className="navbar-card-components shadow-none">
            <CardBody className="max-h-dropdown scrollbar redScroll">
              <div className="nav flex-column">
                {items.map((groupItem, index) => {
                  const NavItemGroup = arraySlicer(groupItem);

                  return (
                    <Fragment key={index}>
                      {index !== 0 && (
                        <div className="nav-link pl-0 py-1 text-900 font-weight-bold">{items[index].name}</div>
                      )}
                      <Row className={index + 1 === items.length ? 'mb-0' : 'mb-3'}>
                        {NavItemGroup.map((navItems, i) => {
                          return (
                            <Fragment key={`${index}-${i}`}>
                              <Col xs={6} xl={3}>
                                {navItems.map((navItem, j) => {
                                  return (
                                    <DropdownItem
                                      tag={Link}
                                      to={navItem.to}
                                      key={`${index}-${i}-${j}`}
                                      className="pl-0 "
                                    >
                                      {navItem.name}
                                      <Badge color={navItem.badge?.color || 'soft-success'} pill className="ml-2">
                                        {navItem.badge?.text}
                                      </Badge>
                                    </DropdownItem>
                                  );
                                })}
                              </Col>
                            </Fragment>
                          );
                        })}
                      </Row>
                    </Fragment>
                  );
                })}
              </div>
            </CardBody>
          </Card>
        )}
        {children}
      </DropdownMenu>
    </UncontrolledDropdown>
  );
};

NavbarDropdownComponents.propTypes = {
  title: PropTypes.string.isRequired,
  items: PropTypes.array,
  right: PropTypes.bool,
  children: PropTypes.node
};

NavbarDropdownComponents.defaultProps = {
  items: [],
  right: false
};

export default NavbarDropdownComponents;
