import React, { Fragment } from 'react';

import classNames from 'classnames';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import {
  Badge,
  Card,
  CardBody,
  Col,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  Row,
  UncontrolledDropdown
} from 'reactstrap';
import AuthCornerImage from '../../assets/img/illustrations/authentication-corner.png';

const NavbarDropdown = ({ title, items, right, children }) => {
  const arraySlicer = routes => {
    const newArr = [];
    routes.map(route => {
      if (route.children) {
        return route.children.map(item => {
          if (item.children) {
            return newArr.push(...item.children);
          }
          return newArr.push(item);
        });
      }
      return newArr.push(route);
    });
    const arrayDivide = title === 'Components' ? Math.ceil(newArr.length / 4) : Math.floor(newArr.length / 3);

    const arrayLength = newArr.length;
    const temp = [];
    for (let i = 0; i < arrayLength; i += arrayDivide) {
      var myArray = newArr.slice(i, i + arrayDivide);
      temp.push(myArray);
    }
    return temp;
  };

  const findTitleName = name => {
    return items.find(routes => {
      if (routes.children) {
        return routes?.children.some(item => {
          return item.name === name;
        });
      }
      return routes.name === name;
    }).name;
  };

  const getTitle = (navItem, index) => {
    if (navItem.name === 'Inbox') {
      return findTitleName(navItem.name);
    }
    if (navItem.name === 'Product list') {
      return findTitleName(navItem.name);
    }
    if (navItem.name === 'Login') {
      return items[index].name;
    }
    if (navItem.name === 'Wizard') {
      return 'Special';
    }
  };

  const NavItemGroup = title !== 'Home' && arraySlicer(items);

  return (
    <UncontrolledDropdown nav inNavbar className="dropdown-menu-on-hover">
      <DropdownToggle nav caret>
        {title}
      </DropdownToggle>
      <DropdownMenu right={right} className="dropdown-menu-card mt-0">
        {items.length > 0 && (
          <Card
            className={classNames('shadow-none max-h-dropdown', {
              'navbar-card-pages': title === 'Pages',
              'navbar-card-auth': title === 'Authentication'
            })}
          >
            {title === 'Authentication' && (
              <img src={AuthCornerImage} alt="" className="position-absolute b-0 r-0" width={130} />
            )}
            <CardBody className={classNames('scrollbar', { 'p-2': title === 'Home' })}>
              <div className="nav flex-column">
                {title === 'Home' ? (
                  items.map(({ to, name }, index) => (
                    <DropdownItem tag={Link} to={to} key={index}>
                      {name}
                    </DropdownItem>
                  ))
                ) : (
                  <Row>
                    {NavItemGroup.map((groupItem, index) => {
                      return (
                        <Col
                          xs={6}
                          key={index}
                          className={classNames('col-xl-3', {
                            'col-xl-4': title === 'Pages',
                            'col-xxl-3 col-xl-6  mb-xxl-0 mb-3': title === 'Authentication'
                          })}
                        >
                          {groupItem.map((navItem, i) => {
                            const title = getTitle(navItem, index);

                            return (
                              <Fragment key={`${index}-${i}`}>
                                {title && <div className="nav-link py-1 pl-0 text-900 font-weight-bold">{title}</div>}
                                <DropdownItem tag={Link} to={navItem.to} key={i} className="pl-0 ">
                                  {navItem.name}
                                  <Badge color={navItem.badge?.color || 'soft-success'} pill className="ml-2">
                                    {navItem.badge?.text}
                                  </Badge>
                                </DropdownItem>
                              </Fragment>
                            );
                          })}
                        </Col>
                      );
                    })}
                  </Row>
                )}
              </div>
            </CardBody>
          </Card>
        )}

        {children}
      </DropdownMenu>
    </UncontrolledDropdown>
  );
};

NavbarDropdown.propTypes = {
  title: PropTypes.string.isRequired,
  items: PropTypes.array,
  right: PropTypes.bool,
  children: PropTypes.node
};

NavbarDropdown.defaultProps = {
  items: [],
  right: false
};

export default NavbarDropdown;
