import React from 'react';
import NavbarDropdown from './NavbarDropdown';
import NavbarDropdownComponents from './NavbarDropdownComponents';
import {
  authenticationRoutes,
  chatRoutes,
  componentRoutes,
  ECommerceRoutes,
  emailRoutes,
  homeRoutes,
  pageRoutes,
  pluginRoutes,
  utilityRoutes,
  widgetsRoutes
} from '../../routes';
import { NavItem, NavLink } from 'reactstrap';
import { Link } from 'react-router-dom';

const DropDownMenus = () => {
  const components = [componentRoutes, pluginRoutes, utilityRoutes];
  const pages = [pageRoutes, widgetsRoutes, chatRoutes, emailRoutes, ECommerceRoutes];
  return (
    <>
      <NavbarDropdown title={homeRoutes.name} items={homeRoutes.children} />
      <NavbarDropdown title={pageRoutes.name} items={pages} />
      <NavbarDropdownComponents title={componentRoutes.name} items={components} />
      <NavbarDropdown title={authenticationRoutes.name} items={authenticationRoutes.children} />

      <NavItem>
        <NavLink tag={Link} to="/documentation">
          Documentation
        </NavLink>
      </NavItem>
    </>
  );
};

export default DropDownMenus;
